.sales-content {
  background-color: @sales-color;
  padding: 10px;
}
.guide-item {
  background-color: #fff;
  margin: 15px 12px;
  padding: 25px 13px;
  &__img-wrapper {
    margin-bottom: 15px;
    padding: 0 12px;
    text-align: center;
  }
  &__content {
    color: #404040;
    font-size: 15px;
    padding: 0 12px;
  }
  &__title {
    font-size: 20px;
    margin-bottom: 10px;
  }
}
.res-links {
  list-style: none;
  margin: 0;
  padding: 0;
  & li {
    display: inline-block;
    &:not(:first-child)::before {
      content: '|';
      margin: 0 9px 0 5px;
    }
  }
  & a {
    color: #404040;
  }
}
.btn-guide {
  background-color: @sales-dark-color;
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 18px;
  line-height: 1.25;
  margin-top: 10px;
  padding: 11px;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
  &:hover,
  &:focus {
    background-color: @sales-color;
    color: #fff;
  }
}
.guide-nav {
  background: 50% no-repeat;
  display: none;
  width: 50px;
  height: 62px;
  position: absolute;
  top: calc(~'50% - 31px');
  &--prev {
    background-image: data-uri('../img/ta/guide-prev.png');
    left: 0;
  }
  &--next {
    background-image: data-uri('../img/ta/guide-next.png');
    right: 0;
  }
}
.guide-pagination {
  list-style: none;
  margin: 15px 0;
  padding: 0;
  text-align: center;
  & li {
    display: inline-block;
    margin: 0 5px;
  }
  & a {
    background-color: @sales-dark-color;
    border: 2px solid @sales-dark-color;
    color: #fff;
    display: block;
    font-size: 28px;
    line-height: 44px;
    text-align: center;
    text-decoration: none;
    transition: background-color .3s linear;
    width: 48px;
    &:hover,
    &:focus {
      background-color: @sales-color;
      color: #fff;
    }
  }
}
@media @tablet {
  .sales-content {
    padding: 35px 0;
  }  
  .guide-list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 50px;
    position: relative;
  }
  .guide-item {
    display: flex;
    flex: 1 1 auto;
    width: calc(~'50% - 30px');
    &__img-wrapper {
      flex: 1 1 42.5%;
      margin-bottom: 0;
      // width: 42.5%;
    }
    &__content {
      flex: 1 1 57.5%;
      // width: 57.5%;
    }
  }
  .guide-nav {
    display: block;
  }
}
@media @tablet-landscape {
  .sales-content {
    padding: 35px;
  }
}