.ta-page-main {
  padding: 0 10px;
}
.ta-main-nav {
  max-width: @section-max-width;
  margin: 30px auto 0;
}
.ta-main {
  max-width: @section-max-width;
  margin: auto;
  padding-bottom: 50px;
}
.ta-top-section {
  margin: auto;
  max-width: @section-max-width;
  padding: 10px 10px 0;
}
.ta-logo-wrapper {
  display: block;
  margin-bottom: 20px;
  text-align: center;
}
.ta-client {
  color: #919191;
  text-align: right;
  &__portrait {
    border: 3px solid #c0c0c0;
    float: right;
    margin-left: 20px;
  }
  &__welcome {
    font-size: 19px;
  }
  &__name {
    font-weight: 700;
  }
  &__hotline-number {
    color: #919191;
    display: block;
    text-decoration: none;
  }
}
.ta-client-number {
  display: block;
  font-size: 22px;
  margin-bottom: 20px;
  text-transform: none;
}
.ta-heading {
  font-size: 32px;
  margin: 40px 0 10px;
  text-transform: uppercase;
}
.ta-main-header {
  .clearfix;
}
.ta-title-block {
  margin: 20px 0 25px;
  padding-left: 88px;
  position: relative;
}
.ta-title {
  border-bottom: 1px solid #d1d1d1;
  color: #404040;
  font-size: 23px;
  font-weight: 600;
  line-height: 1;
  margin: 5px 0 2px;
  max-width: 100%;
}
.ta-description {
  color: #404040;
  font-size: 18px;
}
.ta-thumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
}
.ta-hub-nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 -5px 5px;
  padding: 0;
  &__item {
    flex: 0 0 auto;
    margin: 0 5px 10px;
  }
}
.ta-tile-icon {
  background: 50% no-repeat;
  background-size: 60%;
  padding-bottom: 80%;
  position: relative;
  &--small {
    background-position: 50% 7px;
    background-size: auto 60px;
    padding-bottom: 67%;
    width: 140px;
  }
  &--thumb {
    background-size: auto 40px;
    width: 68px;
  }  
  &--dashboard {
    background-color: @dashboard-color;
  }
  &--news {
    background-color: @news-color;
  }
  &--newsletter {
    background-image: data-uri('../img/ta/newsletter.png');
  }
  &--press-releases {
    background-image: data-uri('../img/ta/press-releases.png');
  }
  &--destinations-news {
    background-image: data-uri('../img/ta/destinations-news.png');
  }
  &--blog-post {
    background-image: data-uri('../img/ta/blog-post.png');
  }
  &--account-overview {
    background-image: data-uri('../img/ta/account-overview.png');
  }
  &--todo {
    background-image: data-uri('../img/ta/todo.png');
  }
  &--sales {
    background-color: @sales-color;
  }
  &--eflyers {
    background-image: data-uri('../img/ta/eflyers.png');
  }
  &--brand {
    background-color: @brand-color;
  }
  &--links {
    background-image: data-uri('../img/ta/links.png');
  }
  &--banners {
    background-image: data-uri('../img/ta/banners.png');
  }
  &--logos {
    background-image: data-uri('../img/ta/logos.png');
  }
  &--contact {
    background-color: @contact-color;
  }
  &--contact-page {
    background-image: data-uri('../img/ta/contact-page.png');
  }
  &--settings {
    background-color: @settings-color;
  }
  &--list-agents {
    background-image: data-uri('../img/ta/list-agents.png');
  }
  &--add-agent {
    background-image: data-uri('../img/ta/add-agent.png');
  }
  &--my-profile {
    background-image: data-uri('../img/ta/my-profile.png');
  }
  &--bank {
    background-color: @bank-color;
    background-image: data-uri('../img/ta/bank.png');
  }
  &__title {
    color: #fff;
    font-size: 12px;
    text-align: center;
    position: absolute;
    right: 0;
    bottom: 5px;
    left: 0;
  }
}
.ta-col-aside {
  margin-bottom: 20px;
}
.ta-col-main {
  margin-bottom: 20px;
}
.ta-col-title {
  font-size: 18px;
  margin-bottom: 10px;
  text-transform: uppercase;
}

// Datepicker
.ui-datepicker.ta-datepicker {
  padding: 0 5px 5px;
  width: 230px;
}
.ui-datepicker-inline {
  width: auto;
}
.ta-datepicker .ui-datepicker-header {
  background-color: transparent;
}
.ta-datepicker .ui-datepicker-title {
  color: #3d4043;
}
.ta-datepicker .ui-datepicker-prev:after, 
.ta-datepicker .ui-datepicker-next:after {
  border: 0;
  color: #3d4043;
  margin-top: -10px;
}
.ta-datepicker .ui-datepicker-prev:after {
  content: '\003c';
}
.ta-datepicker .ui-datepicker-next:after {
  content: '\003e';
}
.ta-datepicker th {
  background-color: transparent;
  border-bottom: 1px solid #e1e3e6;
  font-weight: 700;
  padding: 0 0 3px;
  text-transform: none;
}
.ta-datepicker td {
  border: 0;
}
.ta-datepicker td span {
  color: #c0c4c9;
  line-height: 31px;
}
.ta-datepicker td a {
  border-radius: 50%;
  color: #3d4043;
  line-height: 31px;
  margin: auto;
  max-width: 31px;
  transition: all .3s linear;
}
.ta-datepicker td span:hover, 
.ta-datepicker td span.ui-state-active {
  background-color: transparent;
}
.ta-datepicker .ui-priority-secondary {
    color: #c0c4c9;
}
.ta-datepicker--dashboard .ui-datepicker-week-end a {
  color: @dashboard-dark-color;
}
.ta-datepicker--dashboard td a:hover,
.ta-datepicker--dashboard td a.ui-state-active {
  background-color: @dashboard-dark-color;
  color: #fff;
}
.ta-datepicker--news .ui-datepicker-week-end a {
  color: @news-dark-color;
}
.ta-datepicker--news td a:hover,
.ta-datepicker--news td a.ui-state-active {
  background-color: @news-dark-color;
  color: #fff;
}

@media @tablet {
  .ta-top-section {
    padding: 40px 10px 0;
  }
  .ta-logo-wrapper {
    float: left;
  }
  .ta-client-number {
    float: right;
    margin-left: 10px;
  }
  .ta-title {
    display: inline-block;
    min-width: 230px;
  }
  .ta-flex-row {
    display: flex;
  }
  .ta-col-aside {
    flex: 0 0 auto;
    width: 300px;
  }
  .ta-col-main {
    flex: 1 1 auto;
    margin-left: 20px;
    width: calc(~'100% - 320px');
  }
}
@media @tablet-landscape {
  .ta-top-section {
    padding: 40px 30px 0;
  }
  .ta-hub-nav {
    float: right;
    margin-left: 20px;
    position: relative;
    z-index: 1;
  }
}