  background-color: #ba7d7d;
  padding: 15px;
}
.settings-section {
  background-color: #8d6c6c;
  margin-bottom: 25px;
  padding: 15px;
}
.btn-settings {
  background-color: #484040;
  border: 2px solid #484040;
  color: #fff;
  min-width: 128px;
  padding: 11px 15px 10px;
  text-transform: uppercase;
}
.btn-settings:hover,
.btn-settings:focus {
  background-color: #8d6c6c;
}
.ta-profile-pictures-title {
  font-size: 14px;
  line-height: 1.79;
}
.ta-profile-pictures {
  background-color: #9f7272;
  padding: 10px;
}
.ta-profile-picture {
  background-color: #fff;
  margin: 5px;
  height: 148px;
  overflow: hidden;
  position: relative;
  width: 120px;
}
.ta-profile-picture img {
  vertical-align: top;
}
.ta-profile-picture--add {
  background-color: #d8d8d8;
}
.add-profile-picture {
  background: no-repeat 50% url("data:image/png;base64,");
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.add-profile-picture__content {
  bottom: 0;
  font-size: 13px;
  position: absolute;
  text-align: center;
  text-transform: none;
  width: 100%;
}
.add-profile-picture input[type="file"] {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.btn-del-profile-picture {
  color: #b5b5b5;
  font-size: 19px;
  font-weight: 700;
  line-height: 1;
  position: absolute;
  right: 10px;
  top: 6px;
  transform: scaleX(1.5);
}
.settings-subscribe {
