.settings-content {
  background-color: @settings-color;
  padding: 15px;
}
.settings-section {
  background-color: @settings-dark-color;
  margin-bottom: 25px;
  padding: 15px;
}
.btn-settings {
  background-color: #484040;
  border: 2px solid #484040;
  color: #fff;
  min-width: 128px;
  padding: 11px 15px 10px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background-color: @settings-dark-color;
  }
}
.ta-profile-pictures-title {
  font-size: 14px;
  line-height: 1.79;
}
.ta-profile-pictures {
  background-color: #9f7272;
  padding: 10px;
}
.ta-profile-picture {
  background-color: #fff;
  margin: 5px;
  height: 148px;
  overflow: hidden;
  position: relative;
  width: 120px;
  & img {
    vertical-align: top;
  }
  &--add {
    background-color: #d8d8d8;
  }
}
.add-profile-picture {
  background: no-repeat 50% data-uri('../img/ta/add.png');
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  &__content {
    bottom: 0;
    font-size: 13px;
    position: absolute;
    text-align: center;
    text-transform: none;
    width: 100%;
  }
  & input[type="file"] {
    .sr-only;
  }
}
.btn-del-profile-picture {
  color: #b5b5b5;
  font-size: 19px;
  font-weight: 700;
  line-height: 1;
  position: absolute;
  right: 10px;
  top: 6px;
  transform: scaleX(1.5);
}
.settings-subscribe {
  background-color: #9f7272;
  max-width: 600px;
  &__header {
    background-color: @settings-dark-color;
    color: #fff;
    font-size: 14px;
    padding: 10px 15px;
    text-transform: uppercase;
  }
  &__main {
    padding: 10px 15px;
  }
}
@media @mobile {
  .settings-subscribe {
    &__main {
      display: flex;
      & .ta-form-field {
        flex: 1 1 auto;
        &--controls {
          flex: 0 0 auto;
          margin-top: 0;
        }
      }
    }
  }
}
@media @tablet {
  .settings-form-flex {
    display: flex;
    flex-wrap: wrap;
    & > .ta-form-field {
      flex: 1 1 auto;
      padding: 0 20px;
      width: 50%;
    }
  }
  .settings-form-group {
    flex: 1 1 auto;
    padding: 0 20px;
    width: 50%;
  }
  .ta-profile-pictures {
    padding: 20px;
  }
}
@media @tablet-landscape {
  .settings-content {
    padding: 40px;
  }
  .settings-section {
    padding: 30px 25px;
  }
  .settings-subscribe {
    &__header {
      padding: 10px 45px;
    }
    &__main {
      padding: 10px 45px 25px;
    }
  }
}