// Travel Agents Tabs
.ta-nav-swiper {
  margin: 0 -10px;
  padding: 0 10px 22px;
  &::after {
    content: '';
    background-color: #c6c6c6;
    height: 2px;
    position: absolute;
    right: 0;
    bottom: 20px;
    left: 0;
  }
}
.ta-nav {
  // border-bottom: 2px solid #c6c6c6;
  // display: flex;
  // flex-wrap: wrap;
  list-style: none;
  margin: 0 -5px;
  padding: 0;
  &__item {
    // flex: 0 0 auto;
    margin: 10px 5px 0;
    width: 112px;
  }
}
.ta-nav-tile {
  background-position: 50% 10px;
  background-repeat: no-repeat;
  border: 1px solid #dfdfdf;
  border-bottom: 0;
  color: #000;
  display: block;
  font-size: 12px;
  line-height: 1;
  width: 112px;
  height: 102px;
  padding: 15px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: all .25s linear;
  &::after {
    content: '';
    border-top: 20px solid transparent;
    border-right: 13px solid transparent;
    border-left: 13px solid transparent;
    position: absolute;
    top: 100%;
    left: calc(~'50% - 13px');
  }
  &--dashboard {
    background-image: data-uri('../img/ta/dashboard.png');
    &:hover,
    &.current {
      background-color: @dashboard-color;
      background-image: data-uri('../img/ta/dashboard-hover.png');
      color: #fff;
    }
    &.current {
      &::after {
        border-top-color: @dashboard-color;
      }
    }
  }
  &--news {
    background-image: data-uri('../img/ta/news.png');
    &:hover,
    &.current {
      background-color: @news-color;
      background-image: data-uri('../img/ta/news-hover.png');
    }
    &.current {
      &::after {
        border-top-color: @news-color;
      }
    }
  }
  &--sales {
    background-image: data-uri('../img/ta/sales.png');
    &:hover,
    &.current {
      background-color: @sales-color;
      background-image: data-uri('../img/ta/sales-hover.png');
      color: #fff;
    }
    &.current {
      &::after {
        border-top-color: @sales-color;
      }
    }
  }
  &--brand {
    background-image: data-uri('../img/ta/brand.png');
    &:hover,
    &.current {
      background-color: @brand-color;
      background-image: data-uri('../img/ta/brand-hover.png');
      color: #fff;
    }
    &.current {
      &::after {
        border-top-color: @brand-color;
      }
    }
  }
  &--contact {
    background-image: data-uri('../img/ta/contact.png');
    &:hover,
    &.current {
      background-color: @contact-color;
      color: #fff;
    }
    &.current {
      &::after {
        border-top-color: @contact-color;
      }
    }
  }
  &--policies {
    background-image: data-uri('../img/ta/policies.png');
  }
  &--settings {
    background-image: data-uri('../img/ta/settings.png');
    &:hover,
    &.current {
      background-color: @settings-color;
      background-image: data-uri('../img/ta/settings-hover.png');
      color: #fff;
    }
    &.current {
      &::after {
        border-top-color: @settings-color;
      }
    }
  }
  &--bank-details {
    background-image: data-uri('../img/ta/bank-details.png');
    &:hover,
    &.current {
      background-color: @bank-color;
      background-image: data-uri('../img/ta/bank-details-hover.png');
      color: #fff;
    }
    &.current {
      &::after {
        border-top-color: @bank-color;
      }
    }
  }
  &__title {
    padding: 0 15px;
    position: absolute;
    right: 0;
    bottom: 8px;
    left: 0;
  }
}
@media @tablet-landscape {
  .ta-nav-swiper {
    padding-left: 40px;
    padding-right: 40px;
  }
}