.ta-form-field {
  color: #fff;
  margin-bottom: 10px;
  text-transform: uppercase;
  &--controls {
    margin-top: 20px;
    text-align: center;
  }
  &--right {
    text-align: right;
  }
  &--flex {
    display: flex;
  }
  &--number {
    flex: 1 1 0%;
    padding: 0 2px;
  }
  &--separated {
    padding-left: 10px;
  }
  &--radio {
    line-height: 18px;
    min-height: 25px;
    padding-left: 30px;
    position: relative;
    & input[type="radio"] {
      position: absolute;
      clip: rect(0,0,0,0);
      pointer-events: none;
    }
  }
  &--checkbox {
    padding-left: 30px;
    position: relative;
    & input[type="checkbox"] {
      position: absolute;
      clip: rect(0,0,0,0);
      pointer-events: none;
    }
  }
}
.ta-styled-radio {
  width: 18px;
  height: 18px;
  border: 3px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  input[type="radio"]:checked + & {
    background-color: #432020;
  }
}
.ta-styled-checkbox {
  background-color: #fff;
  width: 18px;
  height: 18px;
  border: 3px solid #fff;
  position: absolute;
  top: calc(~'50% - 9px');
  left: 0;
  input[type="checkbox"]:checked + & {
    background-color: #432020;
  }
}
.form-field-inline-flex {
  margin-right: 15px;
}
.ta-form-control {
  background-color: #fff;
  border: 0;
  color: #000;
  font-size: 15px;
  height: 43px;
  padding: 5px 15px;
  width: 100%;
  textarea& {
    height: 145px;
    vertical-align: top;
  }
  .placeholder(#000);
  &--number {
    // flex: 1 1 auto;
    // margin: 0 2px;
    padding: 5px;
    text-align: center;
    // width: 10%;
  }
}
.ta-form-control--report + .ui-selectmenu-button {
  width: 81px !important;
}
.ta-form-tooltip {
  border-radius: 50%;
  border: 2px solid #fff;
  cursor: default;
  display: inline-block;
  font-size: 18px;
  line-height: 1;
  margin: 0 5px;
  padding: 1px;
  text-align: center;
  width: 24px;
}
.ta-form-field-separator {
  font-size: 14px;
  margin: 15px 0;
  text-align: center;
  text-transform: uppercase;
}
.ta-form-note {
  font-size: 13px;
  font-style: oblique;
  margin-bottom: 25px;
  padding: 0 15px;
  .bank-section & {
    padding: 0;
  }
}
.ta-form-help {
  color: #ffacac;
  font-size: 13px;
}

// jQuery UI Date Picker
.ta-datepicker-control {
  background-image:data-uri('../img/ta/calendar.png');
  background-position: 100% 50%;
  background-repeat: no-repeat;
  cursor: pointer;
  padding-right: 50px;
}
.ta-datepicker-alternate {
  color: #fff;
  font-size: 18px;
  text-align: center;
  &--dashboard {
    background-color: @dashboard-dark-color;
  }
  &--news {
    background-color: @news-dark-color;
  }
}

// jQuery UI Select Menu
.ta-form-field--selectmenu .ui-selectmenu-button {
  border-color: #fff;
}
.ta-form-field--selectmenu span.ui-selectmenu-text {
  line-height: 33px;
  text-transform: none;
}
.ta-form-field--selectmenu span.ui-icon {
  background-color: transparent;
  height: 43px;
  &::after {
    border-top: 16px solid #36352f;
    top: calc(~'50% - 8px');
  }
}

@media @tablet {
  .form-field-inline-flex .ta-form-field,
  .form-group-inline .ta-form-field {
    margin-bottom: 0;
    // margin-right: 15px;
  }
}
@media @tablet-landscape {
  .ta-form-note {
    padding: 0 25px;
  }
  .ta-form-field--separated {
    padding-left: 20px;
  }
}