.ta-news-board {
  list-style: none;
  margin: 35px -10px 0;
  padding: 0;
  &__item {
    padding: 0 10px 30px;
  }
}
.ta-news-tile {
  color: #404040;
  text-decoration: none;
  &:hover,
  &:focus {
    color: #404040;
  }
  &__title {
    border-bottom: 1px solid #c6c6c6;
    font-size: 24px;
    font-weight: 600;
    margin: 15px 0 10px;
    padding-bottom: 3px;
  }
  &__description {
    font-size: 18px;
  }
}
.news-content {
  background-color: @news-color;
  color: #fff;
  padding: 15px;
}
.ta-news-col-section {
  margin-bottom: 10px;
}
.ta-news-col-section-title {
  font-size: 13px;
  margin-bottom: 7px;
}

// News Datepicker
.news-datepicker .ui-datepicker {
  padding: 0 5px 5px;
  width: auto;
}
.news-datepicker .ui-datepicker-header {
  background-color: transparent;
}
.news-datepicker .ui-datepicker-title {
  color: #3d4043;
}
.news-datepicker .ui-datepicker-prev:after, 
.news-datepicker .ui-datepicker-next:after {
  border: 0;
  color: #3d4043;
  margin-top: -10px;
}
.news-datepicker .ui-datepicker-prev:after {
  content: '\003c';
}
.news-datepicker .ui-datepicker-next:after {
  content: '\003e';
}
.news-datepicker .ui-datepicker th {
  background-color: transparent;
  border-bottom: 1px solid #e1e3e6;
  font-weight: 700;
  padding: 0 0 3px;
  text-transform: none;
}
.news-datepicker .ui-datepicker td {
  border: 0;
}
.news-datepicker .ui-datepicker td span {
  color: #c0c4c9;
  line-height: 31px;
}
.news-datepicker .ui-datepicker td a {  
  border-radius: 50%;
  color: #3d4043;
  line-height: 31px;
  margin: auto;
  max-width: 31px;
  transition: all .3s linear;
}
.news-datepicker .ui-datepicker .ui-datepicker-week-end a {
  color: @news-dark-color;
}
.news-datepicker .ui-datepicker td a:hover,
.news-datepicker .ui-datepicker td a.ui-state-active {
  background-color: @news-dark-color;
  color: #fff;
}
.news-datepicker .ui-datepicker td span:hover, 
.news-datepicker .ui-datepicker td span.ui-state-active {
  background-color: transparent;
}
.news-datepicker .ui-datepicker .ui-priority-secondary {
    color: #c0c4c9;
}

// News Templates Swiper
.ta-news-templates-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.ta-news-templates-slide {
  background-color: @news-dark-color;
  height: 135px;
}
.ta-news-templates-buttons {
  cursor: pointer;
  height: 24px;
  margin-top: 10px;
  position: relative;
}
.ta-news-templates-button {
  background: no-repeat 50%;
  width: 24px;
  height: 24px;
  position: absolute;
  top: 0;
  &--prev {
    background-image: data-uri('../img/ta/prev.png');
    left: 0;
  }
  &--next {
    background-image: data-uri('../img/ta/next.png');
    right: 0;
  }
  &.swiper-button-disabled {
    cursor: default;
    opacity: 0.35;
  }
}


// News Post Tabs
.news-post-tabs-nav {
  background-color: @news-dark-color;
  display: flex;
  list-style: none;
  margin: -15px -15px 30px;
  padding: 0;
  & li {
    flex: 0 1 auto;
    width: 85px;
  }
  & a {
    display: block;
    height: 90px;
  }
  & .ui-tabs-active a {
    background-color: @news-color;
  }
}
.tabs-anchor {
  background: no-repeat 50%;
  &--tachometer {
    background-image: data-uri('../img/ta/tachometer.png');
  }
  &--notepad {
    background-image: data-uri('../img/ta/notepad.png');
  }
  &--gallery {
    background-image: data-uri('../img/ta/gallery.png');
  }
  &--chain {
    background-image: data-uri('../img/ta/chain.png');
  }
  &--guestbook {
    background-image: data-uri('../img/ta/guestbook.png');
  }
}
.news-post-tab:not( :nth-child(2) ) {
  display: none;
}

.notepad-title-control {
  height: 60px;
}
textarea.notepad-text-control {
  height: 432px;
  padding: 15px;
}
.notepad-field-actions {
  margin-top: 20px;
}
.ta-notepad-format-options {
  background-color: @news-dark-color;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  & li {
    border-right: 1px solid @news-color;
    flex: 0 1 auto;
    width: 55px;
  }
}
.format-option {
  background: no-repeat 50%;
  border: 0;
  display: block;
  height: 54px;
  width: 100%;
  &--bold {
    background-image: data-uri('../img/ta/bold.png');
  }
  &--italic {
    background-image: data-uri('../img/ta/italic.png');
  }
  &--underline {
    background-image: data-uri('../img/ta/underline.png');
  }
  &--strikethrou {
    background-image: data-uri('../img/ta/strikethrou.png');
  }
  &--unordered-list {
    background-image: data-uri('../img/ta/unordered-list.png');
  }
}

.btn-news {
  background-color: #4c3c3c;
  border: 2px solid #4c3c3c;
  color: #fff;
  font-size: 13px;
  margin: 0 15px 15px 0;
  min-width: 105px;
  padding: 10px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background-color: @news-dark-color;
  }
}

// News Posts Tree
.news-posts-tree {
  text-align: center;
}
.tree-title {
  background-color: @news-dark-color;
  border-radius: 5px;
  display: inline-block;
  font-size: 20px;
  line-height: 1.3;
  margin-top: 15px;
  padding: 7px 15px;
  position: relative;
  z-index: 2;
}
.news-post {
  &__date {
    font-size: 18px;
    margin: 15px 0;
  }
  &__body-inner {
    background-color: #fff;
    border-radius: 5px;
    display: inline-flex;
    padding: 10px;
    text-align: left;
  }
  &__content {
    color: #010101;
    flex: 1 1 auto;
    font-size: 14px;
    padding: 10px;
  }
  &__content-title {
    color: #5c5c5c;
    font-size: 16px;
    font-weight: 700;
  }
  &__img {
    flex: 0 0 auto;
    margin-left: 10px;
    max-width: 78px;
    & img {
      vertical-align: top;
    }
  }
}


@media @small-mobile {
  .ta-news-board {
    display: flex;
    flex-wrap: wrap;
    &__item {
      flex: 0 0 auto;
      width: 50%;
    }
  }
}
@media @tablet {
  .ta-news-board {
    &__item {
      width: 25%;
    }
  }
  .news-post {
    align-items: center;
    display: flex;
    justify-content: center;
    position: relative;
    &::before {
      content: '';
      background-color: #9a8686;
      width: 2px;
      position: absolute;
      top: 0;
      left: calc(~'50% - 1px');
      bottom: 0;
    }
    &::after {
      content: '';
      background-color: #000;
      border: 3px solid #e5e5e5;
      border-radius: 50%;
      width: 6px;
      height: 6px;
      position: absolute;
      top: calc(~'50% - 6px');
      left: calc(~'50% - 6px');
    }
    &__date {
      flex: 1 1 auto;
      margin: 20px;
      text-align: right;
      width: 50%;
    }
    &__body {
      flex: 1 1 auto;
      margin: 20px;
      position: relative;
      text-align: left;
      width: 50%;
      &::before {
        content: '';
        border-right: 10px solid #fff;
        border-top: 8px solid transparent;
        border-bottom: 8px solid transparent;
        position: absolute;
        right: calc(~'100% - 1px');
        top: calc(~'50% - 8px');
      }
    }
    &--even {
      & .news-post__date {
        order: 2;
        text-align: left;
      }
      & .news-post__body {
        order: 1;
        text-align: right;
        &::before {
          border-right: 0;
          border-left: 10px solid #fff;
          left: calc(~'100% - 1px');
          right: auto;
        }
      }
      & .news-post__content {
        order: 2;
      }
      & .news-post__img {
        order: 1;
        margin: 0 10px 0 0;
      }
    }
  }
}
@media @tablet-landscape {
  .news-content {
    padding: 25px 45px;
  }
  .news-content--main {
    padding-left: 130px;
    position: relative;
  }
  .ta-col-aside.news-content {
    padding: 25px 35px;
  }
  .news-post-tabs-nav {
    display: block;
    margin: 0;
    width: 85px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
  }
}