.contact-content {
  background-color: @contact-color;
  color: #fff;
  padding: 15px 15px 1px;
}
.contact-col {
  background-color: @contact-dark-color;
  color: #fff;
  margin: 0 0 20px;
  padding: 15px;
}
.contact-details {
  margin: 0 0 30px;
  &__title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 8px;
  }
  &__content {
    font-size: 14px;
    margin: 0;
    & a {
      color: #fff;
      text-decoration: none;
      &:focus,
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
.btn-contact {
  border: 2px solid transparent;
  margin-top: 20px;
  min-width: 128px;
  padding: 11px 15px 10px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    border-color: #385351;
  }
  &--submit {
    background-color: #385351;
    border-color: #385351;
    &:hover,
    &:focus {
      background-color: @contact-dark-color;
    }
  }
}

// Chat
.chat-actions {
  list-style: none;
  margin: 0 0 20px;
  padding: 0;
  & li {
    display: inline-block;
  }
}
.btn-chat {
  background-position: 50%;
  background-repeat: no-repeat;
  margin: 0 5px;
  width: 30px;
  height: 30px;
  overflow: hidden;
  text-indent: -999em;
  &--save {
    background-image: data-uri('../img/ta/save.png');
  }
  &--call {
    background-image: data-uri('../img/ta/call.png');
  }
  &--close {
    background-image: data-uri('../img/icon-close-white.svg');
    background-size: 15px;
  }
}
.chat-main {
  background-color: #fff;
  height: 242px;
  margin-bottom: 10px;
  overflow-y: scroll;
  padding: 0 15px;
}
.chat-item {
  margin: 15px 0;
  &__body {
    display: flex;
  }
  &__avatar {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    // border: 1px solid @contact-color;
    border-radius: 50%;
    flex: 0 0 auto;
    width: 37px;
    height: 37px;
    .chat-item--self & {
      order: 2;
    }
  }
  &__message {
    background-color: #d8e9e8;
    border-radius: 7px;
    color: #2a504e;
    font-size: 13px;
    margin-left: 15px;
    padding: 10px 15px;
    position: relative;
    &::before {
      content: '';
      border-right: 10px solid #d8e9e8;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
      position: absolute;
      top: 12px;
      right: calc(~'100% - 1px');
    }
    .chat-item--self & {
      margin-left: 0;
      margin-right: 15px;
      order: 1;
      &::before {
        border-right: 0;
        border-left: 10px solid #d8e9e8;
        right: auto;
        left: calc(~'100% - 1px');
      }
    }
  }
  &__time {
    color: @contact-dark-color;
    font-size: 13px;
    margin-top: 5px;
    text-align: right;
    .chat-item--self & {
      text-align: left;
    }
  }
}

@media @mobile {
  .chat-header {
    .clearfix;
  }
  .chat-actions {
    float: right;
  }
}
@media @tablet {
  .contact-col {
    &--aside {
      flex: 1 1 auto;
      width: 20%;
      min-width: 300px;
    }
    &--main {
      flex: 1 1 auto;
      margin-right: 30px;
    }
  }
}
@media @tablet-landscape {
  .contact-content {
    padding: 40px;
  }
  .contact-col {
    padding: 30px 40px 20px;
  }
}