.bank-content {
  background-color: @bank-color;
  color: #fff;
  padding: 15px;
}
.bank-section {
  background-color: @bank-dark-color;
  margin-bottom: 25px;
  padding: 15px;
}
.btn-bank {
  background-color: #422828;
  border: 2px solid #422828;
  color: #fff;
  min-width: 128px;
  padding: 11px 15px 10px;
  text-transform: uppercase;
  &:hover,
  &:focus {
    background-color: @bank-dark-color;
  }
}
.ta-form-field-wrapper {
  text-align: center;
}
.ta-form-field-decorator {
  background-color: #a04444;
  display: inline-block;
  padding: 5px 15px;
  & .ta-form-field {
    margin-bottom: 0;
  }

}
@media @tablet {
  .bank-content {
    padding: 15px 10px;
  }
  .bank-col {
    flex: 1 1 auto;
    margin: 0 5px;
    width: 50%;
  }
}
@media @tablet-landscape {
  .bank-content {
    padding: 40px 25px;
  }
  .bank-col {
    margin: 0 15px;
  }
  .bank-section {
    padding: 30px 25px;
  }
}