.dashboard-content {
  background-color: @dashboard-color;
  color: #fff;
  padding: 15px;
  & label {
    color: #fff;
  }
}

.ta-datepicker-alternate-dashboard {
  background-color: @dashboard-dark-color;
  color: #fff;
  display: flex;
  font-size: 18px;
  & .ta-form-field {
    margin-bottom: 0;
    &:first-child .ta-form-control {
      text-align: right;
    }
  }
  & .ta-form-control {
    background-color: @dashboard-dark-color;
    color: #fff;
    flex: 1 1 auto;
    padding: 5px;
  }
  &__separator {
    flex: 0 0 auto;
    line-height: 33px;
    padding: 5px 0;
  }
}

// Dashboard Table
.dashboard-table {
  font-size: 12px;
  width: 100%;
  & thead {
    background-color: #7f6331;
    color: #fff;
  }
  & th {
    border-right: 1px solid #c8a66c;
    padding: 10px;
    &:last-child {
      border: 0;
    }
  }
  & tbody {
    background-color: #ffd58d;
  }
  & tr {
    border-bottom: 1px solid #c8a66c;
  }
  & td {
    padding: 5px 10px;
    &:last-child {
      border: 0;
    }
  }
}

.report-footer {
  margin-bottom: 20px;
}

// Report Total
.report-total {
  background-color: #ffd58d;
  flex: 0 0 auto;
  font-size: 20px;
  order: 2;
  padding: 30px 20px 15px;
  &__value {
    font-size: 26px;
  }
}

// Report Controls
.report-controls {
  background-color: #ffe7bd;
  flex: 1 1 auto;
  margin-top: 15px;
  order: 1;
  padding: 10px 15px 0;
}
.report-pages {
  margin-right: 10px;
  white-space: nowrap;
}
.report-actions {
  display: inline-block;
  margin-bottom: 10px;
  white-space: nowrap;
}
.btn-report {
  font-size: 20px;
  padding: 5px 10px;
  &--prev {
    &::before {
      content: '\25C0';
    }
  }
  &--next {
    &::before {
      content: '\25b6';
    }
  }
  &:hover,
  &:active {
    background-color: #fff;
  }
}

// Flot
.graph-types {
  list-style: none;
  margin: 0 0 15px;
  padding: 0;
  & li {
    display: inline-block;
  }
}
.btn-type {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: 32px;
  width: 50px;
  height: 50px;
  text-indent: -9999px;
  overflow: hidden;
  &--lines {
    background-image: data-uri('../img/ta/stats-dots.svg');
  }
  &--bars {
    background-image: data-uri('../img/ta/stats-bars.svg');
  }
  &:hover,
  &:focus,
  &--active {
    background-color: #7f6331;
  }
}
.flot-placeholder-wrapper {
  background-color: @dashboard-dark-color;
  padding: 15px;
}
#flot-placeholder {
  width: 100%;
  height: 275px;
}
.flot-tick-label {
  color: #fff;
}
.period-types {
  list-style: none;
  margin: 20px;
  padding: 0;
  & li {
    display: inline-block;
  }
}
.btn-flot-period {
  padding: 5px 10px;
  &:hover,
  &:focus,
  &--active {
    background-color: #7f6331;
  }
}

// TODO
.todo-header {
  padding: 20px 70px 20px 0;
  position: relative;
}
.todo-title {
  color: #ffeac4;
  font-size: 25px;
  line-height: 1.2;
}
.add-todo {
  background-color: #f3bc5b;
  width: 70px;
  height: 70px;
  position: absolute;
  top: 0;
  right: 0;
  text-indent: -9999px;
  overflow: hidden;
  &::before {
    content: '';
    background-color: #a26e13;
    width: 22px;
    height: 8px;
    position: absolute;
    top: calc(~'50% - 4px');
    left: calc(~'50% - 11px');
  }
  &::after {
    content: '';
    background-color: #a26e13;
    width: 8px;
    height: 22px;
    position: absolute;
    top: calc(~'50% - 11px');
    left: calc(~'50% - 4px');
  }
}
.todo-item {
  background-color: #f8ce84;
  background-image: data-uri('../img/ta/todo-item.png');
  background-position: 10px 50%;
  background-repeat: no-repeat;
  color: #000;
  padding: 17px 70px 18px 80px;
  position: relative;
  &:nth-child(even) {
    background-color: #f3bc5b;
  }
  &::after {
    content: '';
    background-color: #a26e13;
    border-radius: 50%;
    width: 13px;
    height: 13px;
    position: absolute;
    top: calc(~'50% - 7px');
    right: 28px;
  }
  &__title {
    font-size: 19px;
    line-height: 1;
  }
  &__description {
    font-size: 13px;
    font-style: oblique;
  }
}
@media @tablet {
  .dashboard-table {
    & tr {
      border: 0;
    }
    & td {
      border-right: 1px solid #c8a66c;
    }
  }
  .flot-header {
    display: flex;
    justify-content: space-between;
  }
  .todo-header {
    width: calc(~'50% - 8px');
  }
  .todo-list {
    column-count: 2;
    column-gap: 16px;
  }
  .todo-item {
    break-inside: avoid;
  }
}
@media @tablet-landscape {
  .dashboard-content {
    padding: 25px 45px;
  }
  .ta-col-aside.dashboard-content {
    padding: 25px 35px;
  }
  .report-footer {
    display: flex;
  }
  .report-controls {
    margin-right: 15px;
  }
  .todo-header {
    width: calc(~'50% - 18px');
  }
  .todo-list {
    column-gap: 36px;
  }
}