.brand-content {
  background-color: @brand-color;
  padding: 15px 10px;
}
.brand-item {
  background-color: #fff;
  margin: 10px;
  padding: 20px 15px;
  text-align: center;
  &__title {
    font-size: 15px;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__description {
    font-size: 14px;
    margin-bottom: 15px;
  }
  &__logo-wrapper {
    margin-bottom: 15px;
  }
}
.btn-brand {
  background-color: @brand-dark-color;
  border-radius: 5px;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 15px;
  line-height: 1.34;
  margin: 5px;
  padding: 8px 5px;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
  &:hover,
  &:focus {
    background-color: @brand-color;
    color: #fff;
  }
  &__title {
    margin-bottom: 5px;
  }
  & a {
    color: #fff;
    &:hover,
    &:focus {
      color: @brand-dark-color;
    }
  }
}
@media @mobile {
  .brand-tiles {
    display: flex;
    flex-wrap: wrap;
  }
  .brand-item {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    width: calc(~'50% - 20px');
    &__logo-wrapper {
      align-items: center;
      display: flex;
      flex: 1 1 auto;
      justify-content: center;
    }
  }
}
@media @tablet-landscape {
  .brand-content {
    padding: 45px 35px;
  }
  .brand-item {
    width: calc(~'25% - 20px');
  }
}